.fill-button {
  position: relative;
  overflow: hidden;
  z-index: 1;
  color: #fff;
  --border-style: none;
  --fill-width: 0%;

  &::before {
    content: '';
    position: absolute;
    background: var(--ion-color-primary-tint);
    top: 0;
    left: 0;
    width: var(--fill-width);
    height: 100%;
    z-index: -1;
    transition: width 0.1s linear;
  }
}
